body {
  height: 100%;
}
#app,
#index {
  height: 100%;
}
#index_box {
  position: absolute;
  left: 0;
  right: 0;
  margin: auto;
  text-align: center;
  border: solid #ccc 1px;
  background-color: #fff;
  z-index: -1;
  -webkit-animation: opening_index_box 3s ease 1s forwards;
          animation: opening_index_box 3s ease 1s forwards;
}
@media screen and (max-width: 8000px) {
#index_box {
      width: 50%;
      top: 285px;
      padding: 50px;
}
}
@media screen and (max-width: 680px) {
#index_box {
      top: 285px;
}
}
@media screen and (max-width: 480px) {
#index_box {
      width: 80%;
      top: 285px;
      padding: 10px;
}
}
#index_box .index_h1 {
    text-decoration: none;
    color: #707070;
}
@media screen and (max-width: 8000px) {
#index_box .index_h1 {
        letter-spacing: 0.3em;
        font-size: 46px;
        font-weight: 300;
}
}
@media screen and (max-width: 680px) {
#index_box .index_h1 {
        font-size: 49px;
}
}
@media screen and (max-width: 480px) {
#index_box .index_h1 {
        font-size: 24px;
}
}
#index_box #index_ul {
    margin-top: 50px;
    padding: 0;
}
#index_box #index_ul .index_li {
      display: inline;
      margin: 20px;
}
#index_box #index_ul .index_li .index_a {
        padding: 5px 20px;
        text-decoration: none;
        border-radius: 5px;
        border: solid #000 0px;
        -webkit-transition: all 0.5s ease-in-out;
        transition: all 0.5s ease-in-out;
        color: #707070;
}
#index_box #index_ul .index_li .index_a:hover {
          color: #eee;
          background-color: #666;
}
@-webkit-keyframes opening_index_box {
0% {
    z-index: -1;
}
99% {
    z-index: -1;
}
100% {
    z-index: 1;
}
}
@keyframes opening_index_box {
0% {
    z-index: -1;
}
99% {
    z-index: -1;
}
100% {
    z-index: 1;
}
}

#opening {
