}
#opening #opeing_h1 {
    position: relative;
    top: 360px;
    letter-spacing: 0.1em;
    -webkit-animation: opening_text 3s ease 1s forwards;
            animation: opening_text 3s ease 1s forwards;
}
@-webkit-keyframes opening {
0% {
    height: 100%;
}
100% {
    height: 0;
}
}
@keyframes opening {
0% {
    height: 100%;
}
100% {
    height: 0;
}
}
@-webkit-keyframes opening_text {
0% {
    top: 360px;
}
100% {
    top: -360px;
}
}
@keyframes opening_text {
0% {
    top: 360px;
}
100% {
    top: -360px;
}
}

