    -webkit-transform: translate(-500px, -20px) rotateZ(360deg) scaleX(3);
            transform: translate(-500px, -20px) rotateZ(360deg) scaleX(3);
}
50% {
    -webkit-transform: translate(-1000px, -500px) rotateZ(720deg) scaleX(3);
            transform: translate(-1000px, -500px) rotateZ(720deg) scaleX(3);
}
75% {
    -webkit-transform: translate(-500px, -250px) rotateZ(360deg) scaleX(3);
            transform: translate(-500px, -250px) rotateZ(360deg) scaleX(3);
}
100% {
    -webkit-transform: translate(1000px, 500px) rotateZ(0deg) scaleX(3);
            transform: translate(1000px, 500px) rotateZ(0deg) scaleX(3);
}
}
@keyframes rotatehr {
